.non-page {
  background: #212121 url('../img/non.svg') no-repeat center/contain;
  max-width: 100%;
  padding-top: 15vh;
  color: #fff;
  text-align: center;
  height: 100vh;
  min-height: 843px;

  & h1 {
    font-size: 24px;
    margin: 0 0 15px;
    text-transform: inherit;
  }

  & p:last-of-type {
    max-width: 495px;
    margin: 0 auto 15vh;
  }
}

.non-page__logo {
  margin-bottom: 20vh;
}

.page__catalog {
  display: flex;
  flex-direction: column;
  align-items: center;

  & .wrapper {
    padding-left: calc(145px + 286px);
    width: 1350px;
    max-width: 100%;
  }
}

.page__footer {
  padding-left: calc(145px + 286px);
  width: 1350px;
  max-width: 100%;
}

@media (max-width: 1180px) {
  .page__catalog .wrapper,
  .page__footer {
    width: 100%;
    padding: 0 145px;
  }
}

@media (max-width: $mobile-width), (max-height: 600px) {
  .wrapper,
  .page__catalog .wrapper,
  .page__footer {
    padding: 0 20px;
    width: 100%;
  }

  .page__title,
  .page__subtitle {
    font-size: 28px;
  }

  .non-page {
    padding: 25px 20px;
    height: auto;
    min-height: calc(100vh - 80px);

    & h1 {
      margin-bottom: 105px;
    }

    & p:last-of-type {
      margin-bottom: 15px;
    }
  }

  .non-page__logo {
    width: 115px;
    margin: 0 auto 90px;
  }
}
