.popap {
  position: fixed;
  background-color: #fff;
  height: 100%;
  width: 100%;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  z-index: 1010;
  padding: 150px 80px 10px;
  background-color: $prime-color;
  box-shadow: 0px 8px 20px rgba(123, 122, 122, 0.25);
  display: none;

  &.popap__see {
    background-color: #212121;
    color: #fff;

    & .popap__cr {
      color: #fff;
    }
  }

  &.popap--download {
    background: #D6D6D6 url('../img/cb-bg.svg') no-repeat top center/100% auto;
  }
}

.popap__wrap {
  width: 1160px;
  max-width: 100%;
  margin: 0 auto;
}

.popap__close {
  position: absolute;
  top: 40px;
  right: 80px;
  font-size: 0;
  width:261px;
  height: 88px;
  display: inline-block;
  background: url('../img/back-w.svg') no-repeat center/contain;
  transition: 0.3s;
  cursor: pointer;

  &:hover,
  &:focus,
  &:active {
    background-image: url('../img/back-hover-w.svg');
  }
}

.popap.popap--download .popap__close {
  background-image: url('../img/back.svg');

  &:hover,
  &:focus,
  &:active {
    background-image: url('../img/back-hover.svg');
  }

}

.popap__form {
  width: 345px;
  max-width: 35%;
  color: #fff;

  & h2 {
    font-size: 44px;
    margin: 0 0 25px;
  }
}

.popap--download .popap__form {
  color: $text-color;
  text-align: center;

  & h2 {
    text-transform: inherit;
  }
}

.popap__image {
  float: right;
  max-width: 50%;
  text-align: center;

  & h2 {
    font-size: 44px;
    margin: 25px 0 20px;
  }
}

.popap__cr {
  color: $text-color;
  text-align: center;
  margin-top: 100px;
}

@media (max-width: $mobile-width) {
  .popap {
    padding: 80px 20px 30px;
    overflow-y: scroll;
  }

  .popap__close {
    top: 10px;
    right: 20px;
    background-size: auto 40px;
    background-position: right center;
  }

  .popap__image {
    float: none;
    max-width: 100%;
    margin: 0 auto 20px;
    text-align: center;

    & h2 {
      font-size: 37px;
      margin: 15px 0;
    }
  }

  .popap__form {
    max-width: 100%;
    margin: 0 auto 20px;

    & h2 {
      font-size: 37px;
      margin: 0 0 15px;
    }
  }

  .popap__cr {
    display: none;
  }
}
