.header__left {
  position: fixed;
  top: 0;
  left: 0;
  width: 135px;
  background: #212121;
  box-shadow: 1px 4px 4px rgba(0, 0, 0, 0.25);
  padding: 25px 0 0;
  text-align: center;
  z-index: 13;
  height: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
}

#left-menu__toggle,
.left-menu__logo--mob {
  display: none;
}

.header__right {
  position: fixed;
  top: 0;
  right: 0;
  width: 135px;
  background: rgba(0, 0, 0, 0.33);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(30px);
  padding: 50px 0 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;

  &.open {
    width: calc(100% - 135px);
    background: rgba(0, 0, 0, 0.5) url('../img/bg-menu.svg') no-repeat center/cover;
    z-index: 99;
  }
}


@media (max-height: 900px) and (min-width: $mobile-width) {
  .header__right,
  .header__left {
    max-height: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: $mobile-width), (max-height: 600px) {
  .header {
    display: flex;
    justify-content: space-between;
    padding: 20px 0;
    align-items: center;
    background: #212121;
    box-shadow: 1px 4px 4px rgba(0, 0, 0, 0.25);
    z-index: 16;
    position: relative;
  }

  .left-menu__logo--desc {
    display: none;
  }

  .header__left,
  .header__right {
    position: static;
    background: none;
    box-shadow: none;
    padding: 0;
  }

  .header__left {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 57%;
    align-items: center;
    min-height: auto;
    padding-left: 20px;

    & > a {
      order: 2;
    }
  }

  #left-menu__toggle,
  .left-menu__logo--mob {
    display: block;
    margin-bottom: 0;
  }

  .header__right {
    min-height: auto;
    width: auto;
  }
}
