.newbuild-radio {
  display: none;
}

.newbuild .type,
.newbuild .catalog-radio,
.newbuild .squere-radio,
.filter--newbuild .type,
.filter--newbuild .catalog-radio,
.filter--newbuild .squere-radio {
  display: none;
}

.newbuild .jk,
.filter--newbuild .jk {
  display: block;
  order: -1;
}

.newbuild .search,
.filter--newbuild .search {
  order: -2;
}

.newbuild .newbuild-radio {
  display: block;
}

.newbuild .filter__input {
  margin-bottom: 20px;
}

.newbuild .filter__title {
  margin-bottom: 14px;
}

.newbuild__grid {
  background: #000 url('../img/nb-bg.jpg') no-repeat center/cover;
}

.newbuild .newbuild__list.wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  width: 100%;
  max-width: 100%;
  padding: 0 120px 0 420px;
}

.newbuild__item {
  width: 33%;
  position: relative;
  transition: 0.3s;
}

.newbuild__img {
  width: 100%;
  height: 100%;
  margin-bottom: -5px;
}

.newbuild__button {
  font-size: 20px;
  position: absolute;
  padding: 20px;
  bottom: 0;
  left: 0;
  z-index: 2;
  opacity: 0;
}

.newbuild__item:hover,
.newbuild__item:focus {
  &::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    border: 4px solid $prime-color;
  }

  & .newbuild__button {
    opacity: 1;
  }
}

@media (max-width: 1180px) {
  .newbuild .newbuild__list.wrapper {
    padding: 0 128px;
  }
}

@media (max-height: 600px) {
  .newbuild .newbuild__list.wrapper {
    padding: 0 20px;
  }
}

@media (max-width: $mobile-width) {
  .newbuild .newbuild__list.wrapper {
    padding: 0 20px;
  }

  .newbuild__item {
    width: 50%;
    flex: 1 1 auto;
  }

  .newbuild__button {
    font-size: 15px;
    padding: 10px;
  }
 }
