.button {
  display: inline-block;
  padding: 15px 25px 14px;
  width: 100%;
  text-decoration: none;
  text-transform: uppercase;
  font-family: $title-font;
  transition: 0.3s;
  background-color: $prime-color;
  font-size: 54px;
  color: $text-color;
  text-align: center;
  -webkit-appearance: none !important;
  -moz-appearance: none !important;
  appearance: none !important;

  &:hover,
  &:focus,
  &:active {
    box-shadow: 8px 20px 40px rgba(138, 100, 11, 0.3);
  }
}

.button--dark {
  background-color: $text-color;
  color: #fff;

  &:hover,
  &:focus,
  &:active {
    background: $prime-color;
    color: $text-color;
  }
}

.button--news {
  text-align: left;
  padding: 5px 60px 4px 10px;
  position: relative;

  &::after {
    content: "";
    width: 32px;
    height: 32px;
    background: #212121;
    position: absolute;
    top: 50%;
    right: 18px;
    margin-top: -16px;
  }
}

.button--form {
  border: none;
  font-size: 22px;
  padding: 13px 13px 11px;
  color: #fff;
  background: $text-color url('../img/button-phone.png') no-repeat center left 13px;

  &:hover,
  &:focus,
  &:active {
    background-color: $prime-color;
    color: $text-color;
    background-image: url('../img/phone-black.png');
  }
}

.button--owner-form {
  border: none;
  font-size: 22px;
  padding: 13px 13px 11px;
}

.button--non-page {
  font-size: 22px;
  padding: 13px 13px 11px;
  max-width: 340px;
}

.button--load {
  background: #F2F2F2;
  color: #A7A7A7;
  margin: 0 10px;

  &:hover,
  &:focus,
  &:active {
    background-color: rgba($text-color, 0.8);
    color: #fff;
    box-shadow: none;
  }
}

.button--more {
  background: #F2F2F2;
  color: #A7A7A7;
  font-size: 50px;
  margin-bottom: 60px;

  &:hover,
  &:focus,
  &:active {
    background-color: rgba($text-color, 0.8);
    color: #fff;
    box-shadow: none;
  }
}

@media (max-width: $mobile-width) {
  .button {
    padding: 8px 25px 7px;
    font-size: 28px;
  }

  .button--load {
    margin: 0;
  }
}
