.breadcrumb {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 22px;
  padding-top: 27px
}

.breadcrumb__link,
.breadcrumb__text {
  font-weight: 500;
  font-size: 12px;
  color: rgba(#000, 0.5);
  margin-right: 15px;
}

.breadcrumb__link {
  text-decoration-line: underline;
  position: relative;

  &:hover,
  &:focus,
  &:active {
    color: $prime-color;
  }

  &::after {
    content: "";
    width: 5px;
    height: 1px;
    background-color: rgba(#000, 0.5);
    position: absolute;
    top: 9px;
    right: -10px;
  }
}

@media (max-width: $mobile-width) {
  .breadcrumb {
    display: none;
  }
}
