.sitemap {
  background: $prime-color;
  min-height: calc(100vh - 38px);

  & .wrapper {
    padding-bottom: 96px;
    background: url('../img/mapsite.svg') no-repeat top left/100% auto,
                url('../img/sm.svg') no-repeat bottom right;
  }

  & h1 {
    padding: 117px 0;
    margin: 0;
    font-size: 44px;
  }
}

.sitemap__list {
  list-style: none;
  padding-left: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  max-height: 430px;
  margin-top: 0;
}

.sitemap__item {
  margin: 0 40px 5px 0;
}

.sitemap__link {
  font-family: $title-font;
  color: $text-color;
  font-size: 24px;
  text-decoration: underline;

  &:hover,
  &:focus,
  &:active {
    color: #fff;
  }
}

@media (max-width: $mobile-width) {
  .sitemap__link {
    font-size: 14px;
  }

  .sitemap .wrapper {
    background-size: 100% auto, auto 70px;
    background-position: top left, bottom center;

    & h1 {
      padding: 60px 0 30px;
      font-size: 28px;
    }
  }
}
