.filter {
  position: fixed;
  top: 0;
  left: 135px;
  background: #212121;
  padding: 10px 0 15px;
  color: #fff;
  width: 286px;
  height: 100%;
  transform: translateY(-300%);
  transition: 0.5s;

  &.open,
  &.show,
  &.open.show {
    transform: translateY(0);
  }
}

.filter__form {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  height: 100%;
  text-align: left;
  max-height: 98vh;
}

.filter__input {
  width: 256px;
  height: 47px;
  padding: 15px 10px;
  margin: 0 15px 5px;
  background: rgba(0, 0, 0, 0.38);
  border: none;
  box-shadow: none;
  color: #fff;

  &::placeholder {
    color: rgba(#fff, 0.36);
  }

  &#search {
    background-image: url('../img/lupa-w.svg');
    background-repeat: no-repeat;
    background-position: center right 10px;
    padding-right: 35px;
  }

  &.filter__select {
    margin-top: 8px;
    background: rgba(0, 0, 0, 0.38) url('../img/filter-select.svg') no-repeat right 20px center;

    & option {
      background: #000;

      &:hover,
      &:focus,
      &:active {
        background-color: rgba(#fff, 0.3);
      }
    }
  }
}


#slider-range,
#slider-range2 {
  margin: 0 15px;
  position: relative;
  margin-bottom: 15px;
}

#squere, #amount{
  display: none;
}

.ui-widget-content {
  background: rgba(#fff, 0.5);
  margin-top: 30px;
  height: 3px;
}

.ui-widget-header {
  background: $prime-color;
  height: 3px;
}

.ui-slider .ui-slider-range {
  position: absolute;
  z-index: 1;
  font-size: .7em;
  display: block;
  border: 0;
  background-position: 0 0;
}


.ui-slider-horizontal .ui-slider-range {
  top: 0;
  height: 100%;
}

.ui-slider .ui-slider-handle {
  position: absolute;
  top: -6px;
  z-index: 2;
  width: auto;
  cursor: pointer;
  text-align: center;
  font-size: 0px;
  margin-left: -.6em;
}

span.ui-slider-handle.ui-corner-all.ui-state-default {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background: $prime-color;
}

.filter__fieldset {
  padding: 0;
  border: none;

  &.type {
    display: flex;
    margin-bottom: 10px;
  }
}

.type input[type="radio"]:checked,
.type input[type="radio"]:not(:checked) {
  width: 93px;
  height: 98px;
  opacity: 0;
  position: relative;
  z-index: 1;
  cursor: pointer;
  margin-right: -93px;
}

.type input[type="radio"]:checked + label,
.type input[type="radio"]:not(:checked) + label {
  display: flex;
  position: relative;
  flex-direction: column-reverse;
  text-align: center;
  padding: 13px 0;
  text-transform: uppercase;
  cursor: pointer;
  font-family: $title-font;
  background-color: #282828;
  font-size: 20px;
  width: 33%;
  transition: 0.3s;

  & svg {
    opacity: 0.25;
    display: block;
    margin: 0 auto 10px;
  }
}

.type input[type="radio"]:checked + label,
.type input[type="radio"]:hover + label {
  color: $text-color;
  background-color: $prime-color;

  & svg {
    opacity: 1;
    fill: $text-color;
  }
}

.filter__title {
  font-size: 12px;
  font-weight: 500;
  opacity: 0.7;
  margin-bottom: 8px;
  margin-left: 15px;
}

.price input[type="radio"]:checked,
.price input[type="radio"]:not(:checked),
.squere input[type="radio"]:checked,
.squere input[type="radio"]:not(:checked) {
  width: 25px;
  opacity: 0;
  position: relative;
  z-index: 1;
  cursor: pointer;
  margin-top: -19px;
  float: right;
  margin-right: 15px;
}

.price input[type="radio"]:checked + label,
.price input[type="radio"]:not(:checked) + label,
.squere input[type="radio"]:checked + label,
.squere input[type="radio"]:not(:checked) + label {
  color: #5F5F5F;
  font-size: 12px;
  font-weight: 500;
  transition: 0.3s;
  float: right;
  margin: -20px 15px 15px 0;
}

.price input[type="radio"]:checked + label,
.price input[type="radio"]:hover + label,
.squere input[type="radio"]:checked + label,
.squere input[type="radio"]:hover + label {
  color: $prime-color;
  text-decoration: underline;
}

.squere-radio {
  display: none;
}

.filter__half {
  display: flex;
  justify-content: space-between;
  margin: 0 15px 10px;
  clear: both;
}

.filter__input--half {
  margin: 0;
  width: 48%;
  padding-left: 30px;
  position: relative;

  &::before {
    font-family: inherit;
    color: #FFFFFF;
    opacity: 0.25;
    position: absolute;
    top: 5px;
    left: 5px;
  }

  &#price-min::before,
  &#squere-min::before {
    content: "От";
  }

  &#price-max::before,
  &#squere-max::before {
    content: "До";
  }
}

.filter__radio input[type="radio"]:checked,
.filter__radio input[type="radio"]:not(:checked) {
  width: 92px;
  height: 26px;
  opacity: 0;
  position: absolute;
  z-index: 1;
  cursor: pointer;
}

.filter__radio input[type="radio"]:checked + label,
.filter__radio input[type="radio"]:not(:checked) + label {
  display: inline-block;
  position: relative;
  padding-left: 35px;
  cursor: pointer;
  margin-bottom: 25px;
  margin-left: 11px;
  line-height: 26px;
}

.filter__radio input[type="radio"]:checked + label:before,
.filter__radio input[type="radio"]:not(:checked) + label:before {
  content: "";
  position: absolute;
  left: 0px;
  top: 0px;
  width: 26px;
  height: 26px;
  border: 3px solid #595959;;
}

.filter__radio input[type="radio"]:checked + label:before {
  border-color: $prime-color;
}

.filter__radio input[type="radio"]:checked + label:after {
  content: "";
  position: absolute;
  left: 7px;
  top: 7px;
  width: 12px;
  height: 12px;
  background-color: $prime-color;
}

.filter__submit {
  padding: 0 15px;
}

footer ~ .newbuild__list {
  position: fixed;
  top: 0;
  right: 120px;
  width: calc(100% - 255px - 286px);
  z-index: 20;
  height: 100%;
  scrollbar-width: none;
  -ms-overflow-style: none;
  overflow-y: scroll;
  transform: translateY(-300%);
  transition: 0.5s;
  max-width: 100%;
  padding: 0;
  display: flex;
  flex-wrap: wrap;

  &::-webkit-scrollbar {
    width: 0;
    background: transparent;
  }

  &.open {
    transform: translateY(0);
  }
}

@-moz-document url-prefix() {
  footer ~ .newbuild__list {
    scrollbar-width: none;
  }
}

.mob-newbuild {
  display: none;
}

@media (min-height: 1080px) {
  .filter__form {
    font-size: 20px;
  }

  .filter__input {
    height: 80px;
    padding: 25px;
  }

  .filter__title {
    font-size: 20px;
  }

  .price input[type="radio"]:checked + label, .price input[type="radio"]:not(:checked) + label, .squere input[type="radio"]:checked + label, .squere input[type="radio"]:not(:checked) + label {
    color: #5F5F5F;
    font-size: 18px;
  }
}

@media (max-height: 900px) and (min-width: $mobile-width) {
  .filter, .filter__form {
    height: auto;
  }

  .type input[type="radio"]:checked,
  .type input[type="radio"]:not(:checked) {
    height: 55px;
  }

  .type input[type="radio"]:checked + label,
  .type input[type="radio"]:not(:checked) + label {
    padding: 5px 0;
    font-size: 15px;
  }

  .type input[type="radio"]:checked + label svg,
  .type input[type="radio"]:not(:checked) + label svg {
    margin-bottom: 5px;
  }

  .type .social__icon {
    width: 36px;
    height: 23px;
  }

  .filter__input {
    height: 43px;
  }

  footer ~ .newbuild__list {
    max-height: 100%;
  }
}

@media (max-height: 768px) and (min-width: $mobile-width) {
  .filter, .filter__form {
    height: auto;
  }

  .filter__fieldset.type {
    margin-bottom: 5px;
  }

  .type input[type="radio"]:checked,
  .type input[type="radio"]:not(:checked) {
    height: 60px;
  }

  .type input[type="radio"]:checked + label,
  .type input[type="radio"]:not(:checked) + label {
    padding: 5px 0;
    font-size: 15px;
  }

  .type .social__icon {
    width: 36px;
    height: 23px;
  }

  .filter__input {
    height: 30px;
    font-size: 11px;
    padding: 5px;
  }

  .filter__title {
    font-size: 11px;
    margin-bottom: 5px;
  }

  .filter__radio input[type="radio"]:checked + label,
  .filter__radio input[type="radio"]:not(:checked) + label {
    padding-left: 30px;
    margin-bottom: 9px;
    line-height: 20px;
  }

  .filter__radio input[type="radio"]:checked + label:before,
  .filter__radio input[type="radio"]:not(:checked) + label:before {
    width: 20px;
    height: 20px;
  }

  .filter__radio input[type="radio"]:checked + label:after {
    left: 6px;
    top: 6px;
    width: 8px;
    height: 9px;
  }

  .filter .button--owner-form {
    font-size: 15px;
    padding: 7px 13px 6px;
  }

  .price input[type="radio"]:checked + label,
  .price input[type="radio"]:not(:checked) + label, .squere input[type="radio"]:checked + label, .squere input[type="radio"]:not(:checked) + label {
    margin: -21px 15px 7px 0;
  }
}

@media (max-width: 1180px) {
  .filter {
    width: calc(100% - 247px);
    margin-bottom: 30px;
    transform: translateY(-400%);

    &.show {
      transform: translateY(-400%);
    }

    &.open.show {
      transform: translateY(0);
    }
  }

  .plitca-block ~ .filter__item,
  .plitca-block ~ .filter__item {
    display: none;
  }

  .type input[type="radio"]:checked, .type input[type="radio"]:not(:checked) {
    width: 33%;
    margin-right: -33%;
  }

  footer ~ .newbuild__list {
    width: calc(100% - 142px - 97px);
    top: 70px;
    transform: translateY(-400%);
    max-height: 774px;
  }

  .mob-newbuild {
    order: -3;
    display: none;
  }

  .filter__button {
    display: inline-block;
    border: none;
    width: 49%;
    text-align: center;
    text-transform: uppercase;
    font-family: $title-font;
    font-size: 20px;
    color: #fff;
    padding: 18px;
    border: none;
    box-shadow: none;
    background: transparent;

    &:hover,
    &:focus,
    &:active,
    &.active {
      background: $prime-color;
      color: $text-color;
    }
  }
}

@media (max-width: $mobile-width), (max-height: 600px) {
  .filter {
    position: absolute;
    top: 236px;
    left: 0;
    width: 100%;
    margin: 0;
    height: auto;
  }

  .filter__form {
    height: auto;
    text-align: center;
  }

  footer ~ .newbuild__list {
    position: absolute;
    width: 100%;
    right: 0;
    top: 304px;
    z-index: 25;
  }
}
