.sort {
  width: 1070px;
  padding-top: 10px;
  max-width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  flex-wrap: wrap;
}

.sort__item {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.sort__title  {
  font-weight: 500;
  font-size: 12px;
  opacity: 0.5;
  margin-right: 5px;
}

.sort__select {
  background: rgba(0, 0, 0, 0.06) url('../img/sort-select.svg') no-repeat center right 10px;
  border: none;
  padding: 15px 30px 15px 10px;
  height: 47px;
  margin-left: 5px;
}

.sort__view {
  display: inline-block;
  width: 27px;
  height: 27px;
  margin-left: 5px;
  margin-bottom: 15px;

  &.active svg {
    fill: $prime-color;
  }
}

.sort__view svg {
  width: 27px;
  height: 27px;
  fill: #F0F0F0;
  display: inline-block;

  &:hover,
  &:focus,
  &:active {
    fill: $prime-color;
  }
}

@media (max-width: $mobile-width) {
  .sort.wrapper {
    justify-content: end;
    margin: 10px auto 0;
    width: 320px;
  }

  .sort__item {
    margin-left: 10px;
  }

  .sort__item:last-child {
    display: none;
  }

  .sort__title  {
    font-size: 9px;
  }

  .sort__select {
    min-width: 87px;
  }
}
