.vacancies .wrapper {
  padding-top: 215px;
  background: url('../img/vacancies.png') no-repeat top 50px center/78% auto;
}

.vacancies__item {
  width: 490px;
  position: relative;
  max-width: 50%;
  margin-bottom: 165px;

  & .gotoform a {
    width: 450px;
    height: 604px;
    position: absolute;
    font-size: 28px;
    font-family: $title-font;
    color: transparent;
    text-transform: uppercase;
    padding: 37% 27%;
    text-align: center;
    top: -150px;
    left: 155%;
    background-size: contain;
    background-position: top center;
    background-repeat: no-repeat;
    transition: 0.3s;
  }

  &:hover .gotoform a,
  &:focus .gotoform a {
    color: #fff;
  }

  &:nth-of-type(even) {
    margin-left: auto;

    & .gotoform a {
      right: 155%;
      left: auto;
    }
  }

  &:nth-of-type(1) .gotoform a {
    background-image: url('../img/vac-1.png');
  }

  &:nth-of-type(2) .gotoform a {
    background-image: url('../img/vac-2.png');
  }

  &:nth-of-type(3) .gotoform a {
    background-image: url('../img/vac-3.png');
  }

  &:nth-of-type(4) .gotoform a {
    background-image: url('../img/vac-4.png');
  }
}

.title-form {
  font-size: 44px;
  margin-bottom: 20px;
  text-align: center;
}

.discr-form {
  font-weight: 500;
  margin-bottom: 55px;
  text-align: center;
}

.form-vacancies {
  width: 700px;
  max-width: 100%;
  margin: 0 auto 150px;
  padding: 0;
  position: relative;

  & .form__label  {
    position: absolute;
    top: -20px;
    left: 0;
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
    color: rgba(0, 0, 0, 0.38);
  }
}

.form__fh {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;

  & > * {
    width: 49%;
  }
}

.owner-form__callback .form__select {
  color: rgba(0, 0, 0, 0.36);
  background: rgba(0, 0, 0, 0.13) url('../img/filter-select.svg') no-repeat right 20px center;
  position: relative;
}

@media (max-width: 1430px) {
  .vacancies__item .gotoform a {
    width: 350px;
    left: 100%;
    top: -70px;
    padding: 28% 18%;
  }

  .vacancies__item:nth-of-type(even) .gotoform a {
    right: 100%;
  }
}

@media (max-width: 710px) {
  .vacancies .wrapper {
    padding-top: 50px;
    background: url('../img/vacancies-mob.png') no-repeat top center/100% auto;
  }

  .vacancies__item {
    padding-top: 150px;
    margin: 0 0 20px !important;
    max-width: 100%;
    width: 100%;

    & .gotoform a {
      top: -40px;
      left: auto;
      width: 100%;
      padding: 100px 0;
      font-size: 18px;
      color: #fff;
      background-size: 225px auto;
      background-position: top center;
    }

    &:nth-of-type(even) .gotoform a {
      right: auto;
    }
  }

  .title-form {
    font-size: 28px;
  }

  .form__fh > * {
    width: 100%;
  }

  .form-vacancies .form__file-label {
    margin-bottom: 90px;
  }

  .form-vacancies .lze-capcha {
    margin-top: -122px;
  }
}
