.card-new .wrapper {
  position: relative;
}

.card-new .new__back {
  top: 50px;
  z-index: 2;
}

.card-new__text {
  float: left;
  width: 50%;
  padding-right: 8px;
}

.card-new__gallery {
  float: right;
  width: 50%;
  position: relative;
}

.card-new__title {
  font-size: 44px;
  margin: 0 0 30px;
}

.card-new__main {
  background: #282828;
  box-shadow: 0px 4px 14px rgba(0, 0, 0, 0.25);
  padding: 14px;
  color: #fff;
  margin-bottom: 11px;

  & img {
    float: right;
    margin-top: 15px;
  }

  & .title {
    font-family: $title-font;
    font-size: 17px;
    margin-bottom: 14px;
  }

  & .value {
    font-family: $title-font;
    font-size: 22px;
    margin-bottom: 0;
    margin-top: 14px;

    & span {
      font-family: $regular-font;
      font-size: 12px;
      opacity: 0.3;
    }
  }
}

.card-new__adres {
  margin-bottom: 15px;
  padding-left: 30px;
  background: url('../img/location-black.svg') no-repeat center left;

  & .title {
    font-size: 12px;
    opacity: 0.3;
    margin-bottom: 8px;
  }

  & .value {
    font-family: $title-font;
    font-size: 17px;
  }
}

.card-new__buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 50px;

  & .button {
    font-size: 22px;
    width: 49%;
  }

  & .button--download  {
    background-image: url('../img/download.svg');
    background-repeat: no-repeat;
    background-position: center left 10px;
  }

  & .button--black  {
    background: $text-color;
    color: #fff;
  }
}

.card-new__video {
  position: absolute;
  bottom: 25px;
  left: 14px;
  z-index: 12;
}

.card-new .swiper {
  margin-top: 80px;
}

.swiper-pagination-fraction {
  display: block;
  width: 100px;
  height: 47px;
  color: rgba(#fff, 0.7);
  top: 0;
  left: 30px;
  padding: 15px 15px 15px 40px;
  background: rgba(0, 0, 0, 0.06) url('../img/camera.svg') no-repeat center left 5px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(30px);
}

.newSwiper .swiper-slide.swiper-slide-next {
  opacity: 0;
}

.card-new__body {
  clear: both;
}

.card-new__subtitle {
  font-size: 44px;
  margin: 0 0 50px;
}

.card-new__list {
  list-style: none;
  padding-left: 0;
  display: flex;
  flex-wrap: wrap;
}

.card-new__list-item {
  width: 33.3%;
  padding-left: 70px;
  background-repeat: no-repeat;
  background-position: center left;
  margin-bottom: 50px;

  & .title {
    font-size: 12px;
    opacity: 0.3;
  }

  & .value {
    font-family: $title-font;
    font-size: 18px;
  }

  &:nth-of-type(1) {
    background-image: url('../img/har-1.svg');
  }

  &:nth-of-type(2) {
    background-image: url('../img/har-2.svg');
  }

  &:nth-of-type(3) {
    background-image: url('../img/har-3.svg');
  }

  &:nth-of-type(4) {
    background-image: url('../img/har-4.svg');
  }

  &:nth-of-type(5) {
    background-image: url('../img/har-5.svg');
  }

  &:nth-of-type(6) {
    background-image: url('../img/har-6.svg');
  }

  &:nth-of-type(7) {
    background-image: url('../img/har-7.svg');
  }

  &:nth-of-type(8) {
    background-image: url('../img/har-8.svg');
  }

  &:nth-of-type(9) {
    background-image: url('../img/har-9.svg');
  }

  &:nth-of-type(10) {
    background-image: url('../img/har-10.svg');
  }

  &:nth-of-type(11) {
    background-image: url('../img/har-11.svg');
  }

  &:nth-of-type(12) {
    background-image: url('../img/har-12.svg');
  }
}

.card-new__plan {
  background: #282828;
  box-shadow: 0px 4px 14px rgba(0, 0, 0, 0.25);
  margin-bottom: 12px;
  display: flex;
  align-items: center;
  color: #fff;
  padding: 3px;

  & .value {
    font-family: $title-font;
    font-size: 22px;
    width: 30%;
    margin: 10px;

    & span {
      font-family: $regular-font;
      font-size: 12px;
      opacity: 0.3;
      padding-left: 10px;
    }

    &.price {
      flex: 1 1 auto;
    }
  }

  &.sold .price {
    text-align: center;
  }

  & .button--plan  {
    width: auto;
    background: #FFFFFF;
    color: $text-color;
    padding: 13px 50px;
    font-size: 22px;

    &:hover,
    &:focus,
    &:active {
      background: $prime-color;
    }
  }
}

.card-new__button {
  margin: 60px 0 80px;
}

@media (max-width: $mobile-width) {
  .card-new__gallery,
  .card-new__text {
    float: none;
    width: auto;
  }

  .card-new__text {
    padding-top: 100px;
  }

  .card-new__text {
    display: flex;
    flex-direction: column;
  }

  .card-new__title {
    order: -2;
    font-size: 28px;
  }

  .card-new__gallery {
    order: -1;
  }

  .card-new .swiper {
    margin-top: 0;
  }

  .card-new__buttons .button {
    font-size: 22px;
    width: 100%;
    margin-bottom: 10px;
  }

  .card-new__main .title {
    font-size: 13px;
  }

  .card-new__main .value {
    font-size: 17px;

    & span {
      font-size: 9px;
    }
  }

  .card-new__main img {
    width: 38%;
  }

  .card-new__adres .value {
    font-size: 14px;
  }

  .card-new__subtitle {
    font-size: 28px;
    margin-bottom: 30px;
  }

  .card-new__list {
    align-items: center;
  }

  .card-new__list-item {
    width: 50%;

    & .title {
      font-size: 7px;
      opacity: 0.7;
      margin-bottom: 5px;
    }

    & .value {
      font-size: 11px;
    }
  }

  .card-new__plan {
    flex-direction: column;
    align-items: flex-start;
    position: relative;

    & .value {
      width: auto;
      font-size: 13px;
      max-width: calc(100% - 78px);

      & span {
        font-size: 8px;
      }
    }

    & .button--plan {
      position: absolute;
      top: 0;
      right: 0;
      font-size: 0;
      background: $prime-color;
      padding: 30px 28px 29px;

      &::after {
        content: ">";
        display: inline-block;
        transform: scaleY(2);
        font-size: 15px;
        color: #fff;
      }
    }
  }

  .card-new__button {
    margin: 30px 0;
    font-size: 23px;
  }
}
