.new .wrapper {
  position: relative;
  padding-top: 210px;
  background: url('../img/news-name.svg') no-repeat top left/60% auto;
}

.new__back {
  position: absolute;
  top: 4%;
  right: 130px;
  font-size: 0;
  width:261px;
  height: 88px;
  display: inline-block;
  cursor: pointer;
  background: url('../img/back.svg') no-repeat center/contain;
  transition: 0.3s;

  &:hover,
  &:focus,
  &:active {
    background-image: url('../img/back-hover.svg');
  }
 }

.new__img {
  float: right;
  margin-left: 30px;
  max-width: 65%;
  width: 775px;
}

.new__title {
  text-transform: inherit;
  font-size: 44px;
  margin-bottom: 50px;
}

.new__data {
  margin-bottom: 50px;

  & .title {
    font-weight: 500;
    font-size: 12px;
    opacity: 0.7;
    margin-bottom: 8px;
  }

  & .value {
    font-family: $title-font;
    font-size: 22px;
    margin-bottom: 0;
    font-weight: bold;
  }
}

.new__subtitle {
  clear: both;
  font-size: 22px;
  text-transform: inherit;
  margin-bottom: 30px;
}

.new__buttons {
  display: flex;
  align-items: center;
  margin-top: 30px;
  position: relative;
}

.btn {
  width: 100px;
  text-align: center;
  background: inherit;
  color: inherit;
  display: inline-block;
  font-size: 0;
  background: #F2F2F2;
  color: #A7A7A7;
  padding: 15px 25px 14px;

  &:hover,
  &:focus,
  &:active {
    background-color: rgba($text-color, 0.8);
    color: #fff;
    box-shadow: none;
  }

  &::after {
    font-size: 54px;
    transform: scaleY(1.45);
  }
}

.btn-prev::after {
  content: "<";
  display: inline-block;
}

.btn-next::after {
  content: ">";
  display: inline-block;
}


@media (max-width: $mobile-width) {
  .new .wrapper {
    padding-top: 110px;
    background-size: 100% auto;
    display: flex;
    flex-direction: column;
  }

  .new__img {
    float: none;
    max-width: 100%;
    margin-left: 0;
    margin-bottom: 25px;
  }

  .new__title {
    order: -1;
    margin-bottom: 25px;
    font-size: 28px;
  }

  .new__back {
    width: 162px;
    height: 52px;
    top: 30px;
    right: 20px;
  }

  .new__data {
    margin-bottom: 0;
  }

  .new__buttons {
    padding-bottom: 60px;
  }

  .btn {
    position: absolute;
    bottom: 0;
    padding: 8px 18px 7px;
    width: auto;

    &::after {
      font-size: 28px;
    }
  }

  .btn-prev {
    left: 0;
  }

  .btn-next {
    right: 0;
  }
}
