.callback {
  background: url('../img/callback-bg.svg') no-repeat center/100% auto;
  padding: 480px 0 450px;
  margin-top: -595px;

  & .wrapper {
    display: flex;
    justify-content: space-evenly;
  }
}

.callback__text {
  max-width: 490px;
  color: #fff;

  & h2 {
    font-size: 44px;
    margin: 0 0 20px;
  }

  .accent {
    color: $text-color;
    text-decoration: underline;
  }
}

.callback__form {
  max-width: 340px;
  margin-top: 65px;
  color: #fff;

  & h3 {
    font-size: 44px;
  }
}

.form__input {
  width: 100%;
  height: 47px;
  padding: 15px;
  background: rgba(0, 0, 0, 0.38);
  backdrop-filter: blur(30px);
  border: none;
  margin-bottom: 20px;
  color: #fff;
}

.form__input::placeholder {
  color: rgba(#fff, 0.36);
}

@media (max-width: 1200px) {
  .callback {
    background-size: cover;
  }
}

@media (max-width: $mobile-width) {
  .callback {
    background: none;
    padding: 0;
    margin-top: 0;

    & .wrapper {
      flex-wrap: wrap;
      text-align: center;
      text-shadow: 0 0 1px #000;
      padding: 0;
    }
  }

  .callback__text {
    width: 100%;
    max-width: 100%;
    padding: 190px 20px;
    background: url('../img/callback-mobtop.svg') no-repeat center right/100% auto;
  }

  .callback__form {
    width: 100%;
    max-width: 100%;
    padding: 190px 20px;
    margin: -200px 0 0;
    background: url('../img/callback-mobbot.svg') no-repeat center right/100% auto;
  }

  .callback__text h2,
  .callback__form h3 {
    font-size: 28px;
  }
}

@media (max-width: 600px) {
  .callback__text {
    padding-bottom: 175px;
    padding-top: 130px;
    position: relative;
    z-index: 2;
  }
  .callback__form {
    margin-top: -1px;
    padding-top: 30px;
    padding-bottom: 175px;
  }
}

@media (max-width: 440px) {
  .callback__text {
    padding-top: 275px;
    margin-top: -100px;
  }
  .callback__form {
    margin-top: -105px;
    padding-top: 100px;
    padding-bottom: 175px;
  }
}
