.card-item {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}

.card-item__text {
  width: 55%;
  padding-right: 30px;
}

.card-item__img {
  width: 45%;
}

.card-item__video {
  position: relative;
  display: inline-block;

  &::after {
    content: "";
    width: 103%;
    height: 100%;
    position: absolute;
    bottom: -1px;
    left: -5px;
    background: url('../img/video.svg') no-repeat center/cover;
  }
}

.card-item__title {
  font-size: 30px;
  margin: 0 0 18px;
}

.card-item__options {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}

.options__item.full {
  width: 100%;
}

.options__item {
  margin-bottom: 20px;

  &:nth-of-type(6) {
    padding: 0 10px;
  }
}

.options__item .title {
  font-weight: 500;
  font-size: 14px;
  color: rgba(#000, 0.3);
  margin-bottom: 5px;
}

.options__item .value {
  font-size: 22px;
  font-family: $title-font;

  &.accent {
    color: $prime-color;
  }
}

.card-item__footer {
  display: flex;
  align-items: center;

  & .card__tag:nth-of-type(even) {
    background: transparent;
  }

  & img {
    filter: brightness(0);
  }
}

.card-item__price {
  font-size: 20px;
  margin-top: 30px;

  & span {
    color: $prime-color;
    font-family: $title-font;
    font-size: 28px;
  }
}

.card-item__back {
  top: -34px;
  right: 0;
}

.card-item__img {
  position: relative;
}

.card-item__slider-full {
  display: inline-block;
  width: 100%;
}

.card-item__select {
  background: rgba(0, 0, 0, 0.06) url('../img/filter-select.svg') no-repeat center right 10px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(30px);
  border: none;
  padding: 15px 30px 15px 15px;
  color: #fff;
  position: absolute;
  top: 80px;
  left: 0;

  & option {
    background: #fff;
    color: #000;
  }
}

.card-item__id {
  background: rgba(0, 0, 0, 0.06);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(30px);
  border: none;
  padding: 15px;
  color: #fff;
  position: absolute;
  bottom: 5px;
  right: 0;
  margin: 0;
}

.card-item__photo {
  position: absolute;
  top: 80px;
  right: 130px;
}

.swiper {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  margin-bottom: 15px;
  padding: 0 30px 30px;
}

.mySwiper .swiper-slide-active img {
  border: 4px solid $prime-color;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.swiper-button-next,
.swiper-button-prev {
  height: calc(100% - 30px);
  top: 0;
  background: $prime-color;
  margin-top: 0;
  color: #fff;
}

.swiper-button-prev {
  left: 0;
}

.swiper-button-next {
  right: 0;
}

.swiper-button-next:after, .swiper-button-prev:after {
  font-size: 12px;
  font-weight: bold;
}

.swiper-scrollbar {
  background: rgba(0, 0, 0, 0.13);
  opacity: 1 !important;
}

.swiper-container-horizontal>.swiper-scrollbar {
  bottom: 15px;
}

.swiper-scrollbar-drag {
  background: $prime-color;
}

.button--see {
  margin-bottom: 44px;
}

.list-more__title {
  font-size: 44px;
  margin: 0 0 57px;
}

.list-more {
  list-style: none;
  padding-left: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 130px;
}

.list-more__item {
  position: relative;

  & .title {
    color: rgba(#fff, 0.7);
  }
}

.list-more__text {
  position: absolute;
  color: #fff;
  bottom: 0;
  left: 0;
  padding-left: 15px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
}

.button--var {
  display: block;
  padding: 5px;
  font-size: 46px;
  position: absolute;
  left: 0;
  bottom: -50px;
  opacity: 0;
  z-index: 10;

  &::after {
    content: "";
    width: 29px;
    height: 32px;
    display: inline-block;
    vertical-align: baseline;
    background: $text-color;
    margin-left: 15px;
  }
}

.list-more__item:hover,
.list-more__item:focus {
  & .button--var {
    opacity: 1;
  }

  &::after {
    content: "";
    width: 100%;
    height: 100%;
    border: 5px solid $prime-color;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
  }
}

@media (max-width: $mobile-width) {
  .card-item__text,
  .card-item__img {
    width: 100%;
  }

  .card-item__back {
    top: 23px;
  }

  .card-item__img {
    margin-top: 140px
  }

  .card-item__title {
    font-size: 24px;
    position: absolute;
    top: 80px;
    left: 0;
  }

  .card-item__text {
    margin-top: 10px;
    order: 2;
    padding-right: 0;
  }

  .options__item {
    margin-bottom: 5px;
  }

  .button--see {
    margin: 20px 0;
    padding: 8px;
  }

  .list-more {
    margin-bottom: 30px;
  }

  .list-more__title {
    font-size: 28px;
    margin: 0 0 30px;
  }

  .list-more__item {
    width: 49%;
    margin-bottom: 1%;

    & img {
      width: 100%;
    }

    & .title {
      font-size: 9px;
    }

    & .value {
      font-size: 12px;
      margin-bottom: 8px;
    }

    .list-more__text {
      padding-left: 6px;
    }
  }

  .button--var {
    font-size: 20px;
    bottom: -34px;

    &.button--var::after {
      width: 22px;
      height: 22px;
      vertical-align: baseline;
      margin-bottom: -4px;
    }
  }
}
