.catalog__item {
  display: flex;
  background: #F3F3F3;
  width: 782px;
  max-width: 100%;
  margin: 0 auto 25px;
  position: relative;
}

.card__top {
  position: absolute;
  top: 55px;
  right: -4px;
  background: #212121;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  padding: 5px 10px;
  z-index: 3;

  & p {
    margin-bottom: 0;
    font-size: 20px;
    font-family: $title-font;
    color: $prime-color;
    text-transform: uppercase;

    & span {
      padding: 15px 13px 10px;
    color: #fff;
    background: url('../img/star.svg') no-repeat center/contain;
    display: inline-block;
    font-size: 16px;
    vertical-align: baseline;
    }
  }
}

.card__text {
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  width: 55%;

  & .full {
    width: 100%;
  }

  & .full.adres {
    padding-right: 40px;
    background: url('../img/location-black.svg') no-repeat center right 20px;
  }
}


.card__item {
  padding: 13px 18px;

  & .title {
    font-weight: 500;
    font-size: 12px;
    opacity: 0.3;
    margin-bottom: 5px;
  }

  & .value {
    font-family: $title-font;
    font-size: 18px;

    & span {
      font-weight: 500;
      font-size: 12px;
      opacity: 0.5;
      font-family: "Montserrat", "PT Sans", sans-serif;
    }

    &.accent {
      color: $prime-color;
      font-size: 24px;
      margin-bottom: 5px;
    }
  }
}

.card__item--title {
  margin: 0;
  font-size: 20px;
}

.card__footer {
  display: flex;
  align-items: stretch;
}

.card__tag {
  display: block;
  text-align: center;
  width: 63px;
  padding: 10px 0;
  position: relative;
}

.catalog__item .card__tag {
  background: #212121;

  &:nth-of-type(even) {
    background: #282828;
  }
}

.tulip {
  background: rgba(#fff, 0.6);
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(30px);
  color: #828282;
  padding: 0;
  border-radius: 10px;
  position: absolute;
  right: 0;
  top: -40px;
  z-index: 2;
  margin: 0;
  width: 0;
  transition: 0.3s;
  overflow: hidden;
}

.card__tag:hover .tulip,
.card__tag:focus .tulip {
  width: 150px;
  padding: 15px 10px;
}

.card__img {
  position: relative;
  width: 45%;
  background: #fff;

  &::after {
    content: "7";
    font-family: "Montserrat", "PT Sans", sans-serif;
    font-weight: 500;
    font-size: 12px;
    color: rgba(#fff, 0.7);
    position: absolute;
    top: 0;
    right: 0;
    display: inline-block;
    padding: 20px 5px 20px 42px;
    background: #212121 url('../img/camera.svg') no-repeat center left 10px;
  }

  & img {
    width: auto;
    height: calc(100% - 57px);
    margin-bottom: -5px;
  }
}

.card__button {
  font-size: 22px;
}

.grid {
  & .catalog {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 0 0 50px;
  }

  & .catalog__item {
    position: relative;
    width: 50%;
    min-height: 394px;
    border: 4px solid transparent;
    box-sizing: border-box;
    margin-bottom: 20px;
  }

  & .catalog__item:hover,
  & .catalog__item:focus {
    border-color: $prime-color;
  }

  .card__item {
    padding: 13px 13px 0;
  }

  & .value.accent {
    margin-bottom: 5px;
  }

  & .card__text {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 2;
    color: #fff;

    & .card__item:first-child {
      margin-top: 50px;
    }

    & .full.adres {
      background-image: url('../img/location.svg');
    }
  }

  & .card__img {
    width: 100%;

    &::after {
      right: auto;
      left: 0;
    }

    &::before {
      content: "";
      width: 100%;
      height: 100%;
      background: linear-gradient(57.62deg, #000000 37.36%, rgba(0, 0, 0, 0) 101.47%);
      position: absolute;
      top: 0;
      left: 0;
    }

    & img {
      width: 100%;
      height: 100%;
    }
  }

  & .card__button {
    position: absolute;
    top: 50%;
    left: 0;
    margin-top: -29px;
    z-index: 3;
    opacity: 0;
  }

  & .catalog__item:hover .card__button,
  & .catalog__item:focus .card__button {
    opacity: 1;
  }

  .gn {
    display: none;
  }

  .gf {
    display: flex;
    align-items: center;

    & .title {
      opacity: 1;
      margin-bottom: 15px;
      margin-right: 5px;
      font-family: $title-font;
      font-size: 18px;
    }
  }

  .value span {
    opacity: 0.5;
  }
}

@media (max-width: 1355px) {
  .grid .catalog__item {
    min-height: 458px;
  }
}

@media (max-width: 1180px) {
  .grid .catalog__item {
    min-height: 394px;
    min-width: 366px;
    margin-bottom: 20px;
  }

  .grid .card__item {
    padding: 13px 9px 0;
  }
}

@media (max-width: $mobile-width) {
  .grid .catalog {
    justify-content: space-evenly;
  }

  .grid .card__text {
    min-height: 100%;
    align-items: flex-end;
  }

  .grid .catalog__item {
    min-height: 300px;
    min-width: 300px;
    margin-bottom: 20px;
  }

  .grid .card__text .card__item:first-child {
    margin-top: 25px;
  }

  .grid .card__text .card__item {
    & .value.accent ~ .value {
      display: none;
    }

    & .title {
      font-size: 10px;
    }

    & .value {
      font-size: 16px;
    }

    .value.accent {
      font-size: 18px;
    }
  }

  .card__img::after {
    font-size: 10px;
    padding: 10px 5px 10px 34px;
    background: transparent url('../img/camera.svg') no-repeat center left 7px/17px auto;
  }

  .grid .card__text .gf .title {
    font-size: 16px;
  }

  .card__tag {
    width: auto;
    padding: 5px;
  }

  .tulip {
    right: auto;
    left: 0;
  }

  .card__footer {
    align-items: stretch;
  }

  .card__tag img {
    max-width: 90%;
  }

  .card__top {
    top: 40px;
    padding: 5px;
    z-index: 3;

    & p {
      font-size: 15px;

      & span {
        font-size: 12px;
      }
    }
  }
}
